@media screen and (min-width: 501px) and (max-width: 1024px) {
	:root {
		--primary-bg-color: #fff;
		--primary-color: #58655a;
		--secondary-color: #575756;
	}

	* {
		margin: 0;
		padding: 0;
		font-family: "Manrope", sans-serif;
		scroll-behavior: smooth;
	}

	ul {
		flex-direction: column;
	}

	h1 {
		font-size: 2.5rem !important;
	}

	.bg-img {
		height: 400vh;
	}

	/* navbar */

	#root>nav {
		flex-direction: column;
		width: 100%;
		height: 4rem;
		position: fixed;
		top: 0rem;
		z-index: 5;
		background-color: rgba(255, 255, 255, 0.9);
	}

	#root>nav>.left {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: rgba(255, 255, 255, 0.9);
	}

	.bi-list {
		display: block;
		width: 2rem;
		height: 2rem;
		margin-right: 2rem;
	}

	#root>nav>.left>.ll {
		font-size: 1.3rem;
		font-weight: 500;
		height: 4rem;
		flex-shrink: 0;
		margin-left: 1.2rem;
		background-color: rgba(255, 255, 255, 0.9);
	}

	#root>nav>.left>.ll>svg {
		width: 1.2rem;
		height: 1.2rem;
		margin-right: 0.6rem;
		padding: 0.4rem;
	}

	#root>nav>.right {
		display: none;
	}

	#root>nav>.mid {
		width: 100%;
		background-color: rgba(255, 255, 255, 0.9);
		height: 0;
		overflow: hidden;
		position: fixed;
		top: 4rem;
		z-index: 5;
	}

	.mido {
		height: auto !important;
		overflow: visible;
	}

	#root>nav>.mid>ul {
		width: 100vw;
	}

	#root>nav>.mid>ul>li {
		margin: 1rem auto;
		width: fit-content;
	}

	li>a {
		font-size: 1.5rem;
		font-weight: 500;
	}

	/* navbar */

	/* home */
	.home {
		margin: 0 auto;
		margin-top: 4.5rem;
		flex-direction: column;
		width: 90%;
		align-items: start;
	}

	.home>.left,
	.home>.right {
		width: 90%;
		margin: auto;
	}

	.right>svg {
		top: 1rem;
	}

	/* home */

	/* about */
	#about {
		height: 35vh;
		overflow: hidden;
	}

	.abt-cont {
		width: 80%;
		flex-direction: column;
		justify-content: start;
		gap: 3rem;
		height: 30vh;
	}

	.lt,
	.rt {
		width: 100%;
	}

	.rt {
		gap: 7%;
	}

	.expcrd {
		width: auto;
	}

	.testi-cont {
		width: 85%;
		margin: 5rem auto;
		align-items: center;
	}

	.l {
		width: 45%;
	}

	.r {
		width: 50%;
	}

	.l>img {
		width: 100%;
		height: 38vh;
		border: 0.4rem solid #ffffff80;
	}

	.r>h2 {
		font-size: 1.3rem;
	}

	.r>p {
		white-space: nowrap;
	}

	.cutting {
		height: 63vh;
		padding: 5rem 0;
	}

	.cut-cont {
		width: 80%;
		margin: auto;
	}

	.cut-cont>video {
		border: 0.4rem solid #ffffff80;
	}

	/* about */

	/* services */
	#services {
		padding-bottom: 0rem;
	}

	.serv-cont {
		width: 90%;
	}

	.servtbbtn {
		flex-direction: column;
		height: 12rem;
		padding: 2% 0;
	}

	.servbtn {
		width: 95%;
	}

	.servtbcont {
		height: 90vh;
		padding: 0;
	}

	.servcont {
		flex-direction: column;
		/* gap: 2.5rem; */
	}

	.sl {
		width: 100%;
		border-radius: 0.375rem;
		object-fit: cover;
		object-position: center;
	}

	.sr {
		width: 90%;
		/* height: 40vh!important; */
	}

	.slo,
	.sro {
		height: 40vh !important;
	}

	.sr>h1 {
		font-size: 1.5rem !important;
		font-weight: 600;
	}

	.servlist {
		height: 85vh;
	}

	.servlc {
		width: 85%;
	}

	.serv {
		width: 45%;
		margin-top: 1.5rem;
	}

	/* services */

	/* gallery */
	#gallery {
		width: 90%;
	}

	.gpics {
		width: 100vw;
	}

	.img {
		width: 90vw;
		height: 52vh;
	}

	.img>img {
		height: 50vh;
	}

	/* gallery */

	/* FAQ */
	#FAQ {
		width: 90%;
		flex-direction: column;
	}

	#FAQ>.Fleft {
		width: 100%;
	}

	.Fleft>svg {
		/* left: 83%; */
		top: 1rem;
	}

	.Fright {
		width: 100%;
	}

	.qna {
		width: 90%;
		max-height: 4.5rem;
	}

	.question {
		height: 4.5rem;
	}

	.question>h3 {
		font-weight: 600;
	}

	.contact {
		height: 80vh;
	}

	.ccont {
		flex-direction: column;
		width: 90%;
		align-items: start;
		gap: 2rem;
		justify-content: start;
	}

	.cleft,
	.cright {
		width: 100%;
	}

	.ccard {
		width: 81%;
	}

	/* FAQ */

	/* Footer */
	.footer {
		width: 100vw;
		height: 28rem;
		overflow: hidden;
		padding-top: 3rem;
	}

	.footer>nav {
		flex-direction: column;
		width: 90%;
		height: auto;
	}

	.footer>nav>.left {
		justify-content: center;
		margin-bottom: 2rem;
		width: 100%;
	}

	.footer>nav>.mid {
		width: 100%;
	}

	.footer>nav>.mid>ul {
		flex-direction: row;
		justify-content: space-evenly;
	}

	.footer>nav>.mid>ul>li {
		width: max-content;
		margin: 0;
	}

	.footer>nav>.mid>ul>li>a {
		font-size: 1rem;
	}

	.footer>nav>.left>p {
		font-size: 0.9rem;
		white-space: wrap;
		width: 100%;
		margin: 0;
		margin-bottom: 0.5rem;
	}

	.fl2 {
		flex-direction: row;
		width: 95% !important;
		margin: auto;
		margin-top: 2rem;
		gap: 0.5rem;
	}

	.d {
		display: block;

	}

	.m {
		display: none;
	}

	/* Footer */
}